*{
  box-sizing: border-box;
}

body {
  margin: 0;
  background-color: $mainBackground;
  color: $lightTextColor;
  font-family: Nunito Sans, Roboto, Arial, Helvetica, sans-serif;
  box-sizing: border-box;
}

a {
  display: block;
  text-decoration: none;
}

p,
h1,
h2,
h3,
ul {
  margin: 0;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}
input {
  padding: 0;
  margin: 0;
  border: 0;
  border: none;
}
ul {
  list-style: none;
  padding: 0px;
}

.section{
  width: 100%;
}

button{
  cursor: pointer
  
}

